@use "abstracts" as *;
@use "components/main-container";


@mixin content{
      height: 30vh;
          width: 50%;
          display: flex;
          justify-content: space-around;
          align-items: center;
  }
  html{
    font-size: 62.5%;
    overflow: hidden;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Gotham Rounded', sans-serif;
    text-decoration: none;
    color: white;
  }
  
  body{
      height: 100vh;
      width: 100vw;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      background-color: #70a1ff;
      background-image: linear-gradient(315deg, #70a1ff 0%, #c2c0c0 74%);
      
      .information{
          @include content;
          flex-direction: column;
          h1{
            padding-top: 3rem;
              font-size: 3rem;
          }
        }
        
        
        .degree-section{
          
          display: flex;
          align-items: center;
          color: #4683f5;
          
          
          h2{
            font-size: 4rem;
            cursor: pointer;
          }
          span{
            margin: 1rem;
            font-size: 1.5rem;
          }
        }
        .temperature-description{
            flex-direction: column;
            
            color: white;
            font-size: 3rem;
          }
          .city-search-container {
            display: flex;
            justify-content: center;
          }
          
          .city-search {
            margin-top: 2rem;
            font-size: 2rem;
            outline: none;
            border: 0.2rem solid #777;
            border-radius: 1em;
            padding: 0.8rem 1.5rem;
            width: 100%;
            text-align: center;
          }
      }

    ///////////////Loading Animation//////////////////
    .loader-container{
        position: fixed;
        top:0; left:0;
        height:100%; 
        width:100%;
        z-index: 10000;
        background-color: #70a1ff;
        background-image: linear-gradient(315deg, #70a1ff 0%, #c2c0c0 74%);
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        transition: all .5s ease-in-out;
            img{
             width:35rem;
            }
            .loader-ring {
              position: absolute;
              top: 50%;
              left: 50%;
              margin: -120px 0 0 -120px;
              width: 240px;
              height: 240px;
              .loader-ring-light {
                width: 240px;
                height: 240px;
                -moz-border-radius: 240px;
                -webkit-border-radius: 240px;
                border-radius: 240px;
                -moz-box-shadow: 0 4px 0 #00abf2 inset;
                -webkit-box-shadow: 0 4px 0 #00abf2 inset;
                box-shadow: 0 4px 0 #00abf2 inset;
                animation: rotate-360 2s linear infinite;
              }
              
              .loader-ring-track {
                position: absolute;
                top: 0;
                left: 0;
                width: 240px;
                height: 240px;
                -moz-border-radius: 240px;
                -webkit-border-radius: 240px;
                border-radius: 240px;
                -moz-box-shadow: 0 0 10px 4px rgba(0, 0, 0, 0.3) inset;
                -webkit-box-shadow: 0 0 10px 4px rgba(0, 0, 0, 0.3) inset;
                box-shadow: 0 0 10px 4px rgba(0, 0, 0, 0.3) inset;
              }
            }
            
            
            @keyframes rotate-360 {
              from {
                -moz-transform: rotate(0);
                -ms-transform: rotate(0);
                -webkit-transform: rotate(0);
                transform: rotate(0);
              }
              to {
                -moz-transform: rotate(360deg);
                -ms-transform: rotate(360deg);
                -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
              }
            }
        }
        
        .fade-out{
          z-index: -1;
            opacity:0;
        }


 